* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: whitesmoke;
    text-align: center;
    min-height: 100vh;
    font-family: montserrat;
}

.title {
    margin-top: 20px;
    margin-bottom: 20px;
    letter-spacing: 5px;
    font-size: 2.2rem;
}

.flex-search {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 40px;

}

.image-style {
    width: 400px;
    object-fit: cover;
    filter: grayscale(100%);
    display: block;
    margin: 50px auto;
}

.image-style2 {
    width: 400px;
    object-fit: cover;
    display: block;
    margin: 50px auto;
}

.button {
    box-shadow: 0 0.1rem rgb(121, 121, 121, 0.35);
}

.button-flex {
    display: flex;
    flex-direction: row;
    gap: 20px;
    justify-content: center;
}

.loader {
    margin: 200px auto
}

/* MODAL STYLES */


.modal-content-fle {
    display: flex;
    flex-direction: column;
}

.modal-content {
    font-size: 1rem;
    font-weight: 600;
    padding: 1.75rem 3.5rem;
    margin: 5rem 5rem 5rem 5rem;
    color: #444;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 0;
    left: 10;
    margin-right: 10px;
    font-size: 1em;
    font-size: 1.2rem;
    color: #444;
    background-color: #FFF;
    border: none;
    cursor: pointer;
}